import { cookies } from "next/headers";
import { redirect } from "next/navigation";
import GoBack from "@/app/Components/buttons/GoBack";

export default function LoginPage() {
  async function handleLogin(formData: FormData) {
    "use server"; // Mark this function as a server action

    const email = formData.get("email") as string;
    const password = formData.get("password") as string;
    const role = formData.get("role") as string;

    // Hardcoded admin credentials
    const ADMIN_EMAIL = "admin@example.com";
    const ADMIN_PASSWORD = "admin123";

    // Simulate authentication
    if (role === "admin" && email === ADMIN_EMAIL && password === ADMIN_PASSWORD) {
      // Set cookies for session management
      const cookieStore = await cookies();
      cookieStore.set("auth-token", "admin-token");
      cookieStore.set("user-role", "admin");

      // Redirect to admin dashboard
      redirect("/admin/dashboard");
    } else {
      // Handle invalid credentials
      redirect("/auth/login?error=Invalid credentials");
    }
  }

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-white p-6">
      <h1 className="text-3xl font-bold text-gray-900 mb-8">Login</h1>
      <form action={handleLogin} className="w-full max-w-sm bg-white p-8 rounded-lg shadow-md">
        <div className="mb-4">
          <label className="block text-sm font-medium text-gray-700">Email</label>
          <input
            type="email"
            name="email"
            required
            className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
          />
        </div>
        <div className="mb-4">
          <label className="block text-sm font-medium text-gray-700">Password</label>
          <input
            type="password"
            name="password"
            required
            className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
          />
        </div>
        <div className="mb-4">
          <label className="block text-sm font-medium text-gray-700">Role</label>
          <select
            name="role"
            required
            className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
          >
            <option value="admin">Admin</option>
            <option value="manager">Manager</option>
          </select>
        </div>
        <button
          type="submit"
          className="w-full px-4 py-2 bg-indigo-600 text-white rounded-md hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500"
        >
          Login
        </button>
      </form>
      <GoBack />
    </div>
  );
}